// placeholder
// ===============


@mixin iph() {
	&.placeholder {
		@content;
	}
	&:-moz-placeholder {
		@content;
	}
	&::-moz-placeholder {
		@content;
	}
	&:-ms-input-placeholder {
		@content;
	}
	&::-webkit-input-placeholder {
		@content;
	}
}

// =============================================
// =============================================

@mixin animated() {
	animation-duration: 1s;
	animation-fill-mode: both;
}