.checkbox {
	display: inline-block;
	vertical-align: top;
	&_size_small {
		font-size: 14px;
		line-height: 1.714;
		.checkbox__label {
			min-width: 20px;
			min-height: 20px;
			&:before {
				width: 20px;
				height: 20px;
				border-width: 1.5px;
				border-color: rgb(214, 217, 229);
				border-style: solid;
				border-radius: 5px;
			}
		}
		.checkbox__label-in {
			padding-left: 55px;
			min-height: 20px;
		}
		.checkbox__checked {
			font-size: 11px;
			left: 5px;
		}
	}
