		display: block !important;
	}
	.owl-stage {
		@include mlg() {

		}
	}
	.owl-item {
		@include mlg() {

		}
		&.active {
			@include mlg() {
				// display: block;
			}
		}
	}
}
.quiz__step {
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-direction: column;
	padding-left: 15px;
	padding-right: 15px;
	@include lg() {
		max-width: 700px;
	}
}
.quiz__step-in {
	display: flex;
	flex-direction: column;
	justify-content: center;
	min-height: 713px;
}
.quiz__link {

}
.quiz__checkbox {
	margin-bottom: 0;
	// padding-bottom: 0;
}

.quiz__buttons {
	// margin-top: 40px;
	& > * {
		margin-left: 15px;
		margin-right: 15px;
	}
}

.quiz__close {
	position: absolute;
	top: 15px;
	right: 15px;
}